/**
 * CSV
 * @param {string} csvContent CSV
 * @returns {array} 
 */
export function parseParticipantsCSV(csvContent) {
  const lines = csvContent.trim().split('\n');
  const participants = [];

  // 
  for (let i = 1; i < lines.length; i++) {
    const line = lines[i].trim();
    if (!line) continue;

    const parts = line.split(',');
    if (parts.length >= 2) {
      participants.push({
        name: parts[0].trim(),
        type: parts[1].trim(),
        extraTimes: parts[2] ? parseInt(parts[2].trim()) : 0
      });
    }
  }

  return participants;
}

/**
 * CSV
 * @param {string} csvContent CSV
 * @returns {object} 
 */
export function parsePrizesCSV(csvContent) {
  const lines = csvContent.trim().split('\n');
  const prizes = {};

  // 
  for (let i = 1; i < lines.length; i++) {
    const line = lines[i].trim();
    if (!line) continue;

    const parts = line.split(',');
    if (parts.length >= 4) {
      const level = parts[0].trim();
      const name = parts[1].trim();
      const quantity = parseInt(parts[2].trim());
      const eligibility = parts[3].trim();

      if (!prizes[level]) {
        prizes[level] = [];
      }

      prizes[level].push({
        name,
        quantity,
        eligibility
      });
    }
  }

  return prizes;
}

export function conversionCategoryName(key) {
  let name = '';
  switch (key) {
    case 'firstPrize':
      name = '';
      break;
    default:
      break;
  }
  const newLottery = getData(newLotteryField) || [];
  const findres = newLottery.find(item => item.key === key);
  if (findres) {
    name = findres.name;
  }
  return name;
}
